<template>
  <div>
    <detail-banner :sightName="sightName" :bannerImg="bannerImg" :gallaryImgs="gallaryImgs"></detail-banner>
    <detail-header :sightName="sightName"></detail-header>
    <detail-list :list="list"></detail-list>
    <div class="content"></div>
  </div>
</template>
<script>
import DetailBanner from "./components/Banner";
import DetailHeader from "./components/Header";
import DetailList from "./components/List";
import axios from "axios";
export default {
  name: "Detail",
  components: {
    DetailBanner,
    DetailHeader,
    DetailList
  },
  data() {
    return {
      sightName: "",
      bannerImg: "",
      gallaryImgs: [],
      list: [],
      pageId: 0
    };
  },
  methods: {
    getDetailInfo() {
      axios
        .get("/api/detail.json", {
          params: {
            id: this.$route.params.id
          }
        })
        .then(this.handleGetDataSucc);
    },
    handleGetDataSucc(res) {
      res = res.data;
      if (res.ret && res.data) {
        const data = res.data;
        this.sightName = data[this.pageId].sightName;
        this.bannerImg = data[this.pageId].bannerImg;
        this.gallaryImgs = data[this.pageId].gallaryImgs;
        this.list = data[this.pageId].priceType;
      }
    }
  },
  mounted() {
    this.getDetailInfo();
  },
  activated() {
    this.getDetailInfo();
    this.pageId = Number(this.$route.params.id) - 1;
  }
};
</script>
<style lang="stylus" scoped>
.content {
  height: 50rem;
}
</style>


